	export default {
		data() {
			return {
                num: 0,
                prize_list: [
                    {
                        'image':'/static/xiexie.png',
                        'name':'谢谢参与'
                    },
                    {
                        'image':'/static/alljinbi.png',
                        'name':'100金币'
                    },
                    {
                        'image':'/static/quan.png',
                        'name':'1次'
                    },
                    {
                        'image':'/static/alljinbi.png',
                        'name':'500金币'
                    },
                    {
                        'image':'/static/rmb.png',
                        'name':'100元现金'
                    },
                    {
                        'image':'/static/alljinbi.png',
                        'name':'50金币'
                    },
                    {
                        'image':'/static/xiexie.png',
                        'name':'谢谢参与'
                    },
                    {
                        'image':'/static/quan.png',
                        'name':'3次'
                    },
                    {
                        'image':'/static/rmb.png',
                        'name':'50元现金'
                    },
                    {
                        'image':'/static/quan.png',
                        'name':'2次'
                    },
                    {
                        'image':'/static/rmb.png',
                        'name':'30元现金'
                    },
                    {
                        'image':'/static/alljinbi.png',
                        'name':'200金币'
                    }
                ],
                rotate_angle: 0, //将要旋转的角度
                rotate_transition: "transform 6s ease-in-out", //初始化选中的过度属性控制
                click_flag: true,
                rand_circle: 10,
                start_rotating_degree: 0,
                val: '',
                isPrize: false,
                prizeText: '谢谢参与',
                prizeBtn: '返回抽奖',
                result_angle: [15,45,75,105,135,165,195,225,255,285,315,345],
                prize_people: [
                    "恭喜DFG225D抽中30元红包",
                    "恭喜E42VS22抽中50元红包",
                    "恭喜E746A4G抽中100元红包",
                    "恭喜ERGA561抽中30元红包",
                    "恭喜AG4465S抽中50元红包",
                    "恭喜ASDGA16抽中100元红包",
                    "恭喜RTJET6H抽中30元红包",
                    "恭喜BDG1N64抽中50元红包",
                    "恭喜ERYH6ZD抽中100元红包",
                    "恭喜F1S6T1B抽中30元红包",
                    "恭喜AGE11WE抽中50元红包",
                    "恭喜AERG46D抽中100元红包"
                ],
                autoplay: false
			}
		},
		methods: {
            prizeRule(){





            },
			rotate_handle(){
                let loginflag = this.$store.state.isLogin;
                let userid = this.$store.state.userInfo.userId;
                if (!loginflag) {
                    uni.navigateTo({
                        url:"/pages/login/login",
                        animationType:"slide-in-bottom"
                    });
                    return
                }
                if (!this.click_flag) {
                    return
                }
                if (this.num == 0) {
                    return
                }
                this.click_flag = false;
                this.requestData();
            },
            rotating(){
                let during_time = 5; // 默认为1s
                let result_index;
                result_index = this.val;
                var rotate_angle = this.start_rotating_degree + this.rand_circle * 360 +this.result_angle[result_index] - this.start_rotating_degree % 360;
                this.start_rotating_degree = rotate_angle;
                this.rotate_angle = "rotate(" + rotate_angle + "deg)";
                var that = this;
                // 旋转结束后，允许再次触发
                setTimeout(function() {
                    that.click_flag = true;
                    that.game_over();
                }, during_time * 1000 + 1500); // 延时，保证转盘转完
            },
            game_over(){
                if (this.prize_list[this.val].name == '谢谢参与') {
                    this.isPrize = false;
                } else {
                    this.isPrize = true;
                }





                this.requestTimer();
            },
            requestData(){
                let userid = this.$store.state.userInfo.userId;
                uni.request({
                    url: this.websiteUrl + '/api/lottery/getprize',
                    method: 'GET',
                    data: {
                        userId: userid
                    },
                    success: res => {
                        if (res.data.status == 0) {
                            this.val = res.data.result;
                        } else {
                            this.val = 11;
                        }
                        this.rotating()
                    },
                    fail: () => {},
                    complete: () => {}
                });
            },
            requestTimer(){
                let userid = this.$store.state.userInfo.userId;
                if (!userid) {
                    userid = '';
                }
                uni.request({
                    url: this.websiteUrl + '/api/task/lotterycount',
                    method: 'GET',
                    data: {
                        userId: userid
                    },
                    success: res => {
                        if (res.data.status == 0) {
                            this.num = res.data.result;
                        }
                    },
                    fail: () => {},
                    complete: () => {}
                });
            },
            requestPrize(){
                uni.request({
                    url: this.websiteUrl + '/api/lottery/prizes',
                    method: 'GET',
                    data: {},
                    success: res => {
                        if (res.data.status == 0) {
                            this.prize_list = res.data.result;
                        }
                    },
                    fail: () => {},
                    complete: () => {}
                });
            }
		},
        onShow() {
            this.requestTimer();
            this.autoplay = !this.autoplay;
        },
        onHide() {
            this.autoplay = !this.autoplay;
        },
        onLoad() {
            this.requestPrize();
        },
        onUnload() {
            this.autoplay = !this.autoplay;
        }
	}
